.card {
  @apply border rounded-8px border-surface-variant;
  @apply bg-surface+3;
  @apply min-w-320px min-h-140px;
  @apply inline-flex;
  @apply overflow-hidden;
  @apply cursor-pointer;
  @apply transition-shadow;

  &:hover {
    @apply shadow-evaluation-2;
  }

  &:active {
    @apply shadow-transparent;
    @apply bg-surface-sl-low;
  }

  &__left-side {
    @apply w-100px;
  }

  &__right-side {
    @apply flex flex-col justify-between;
    @apply p-sm;
    @apply text-on-surface;
  }

  &__card-image {
    @apply w-100%;
  }

  &__rating {
    @apply flex items-center;
    @apply text-caption-lg font-inter font-semibold;
  }

  &__rating-element {
    @apply flex items-center mr-none+3;
    svg {
      @apply mr-none+3 -ml-none+3;
      @apply fill-primary scale-75;
    }
  }

  &__address {
    @apply text-caption-lg font-inter font-normal text-on-surface-variant;
  }

  &__time-slots {
    @apply flex;
  }

  &__time-slot {
    @apply mr-none+4;
    &:last-child {
      @apply mr-none;
    }
  }
}
