.input {
  $base: &;
  $input: #{$base}__input-element;
  @apply relative;

  
  input[type="text"]#{$input},
  input[type="password"]#{$input},
  input[type="number"]#{$input} {
    @apply font-inter font-normal text-body-lg text-on-surface;
    @apply h-3xl p-none+1 pl-sm;
    @apply border-outline-variant border rounded-8px;
    @apply placeholder-on-surface;
    @apply w-100%;
    @apply transition-colors;

    &:disabled {
      @apply bg-surface+3;
      @apply cursor-not-allowed;
      @apply opacity-40;
    }
    
    &::placeholder {
      @apply text-on-surface-variant;
    }

    &:hover:enabled {
      @apply bg-surface-sl-low;
    }

    &:focus:enabled {
      @apply border-2 border-primary;
      @apply text-on-surface;
      @apply outline-none;
      @apply p-none pl-xs+7;
      box-shadow: none;
    }
  }  

  &__icon-container {
    @apply absolute top-none left-none bottom-none flex items-center px-xs+4;
  }

  &__icon {
    @apply fill-on-surface-variant;
  }

  &__label-container {
    @apply absolute top-none left-sm bottom-none pt-sm;
    @apply pointer-events-none;
    @apply transition-all;
  }

  &__label {
    @apply font-inter font-normal;
    @apply pointer-events-none;
    @apply text-on-surface; 
    @apply transition-all;
  }


  &__close-button-container {
    @apply absolute right-none top-none bottom-none flex items-center px-none+5;
    &__clear-button {
    }
  }

  &--icon-visible {
    input[type="text"]#{$input},
    input[type="password"]#{$input},
    input[type="number"]#{$input}  {
      @apply pl-xl+7;

      &:focus:enabled {
        @apply pl-xl+6;
      }
    }

    #{$base}__label-container {
      @apply left-xl+7;
    }
  }

  &--label-visible {
    input[type="text"]#{$input},
    input[type="password"]#{$input},
    input[type="number"]#{$input}  {
      @apply pt-sm+4;

      &:focus:enabled {
        @apply pt-sm+3;
      }
    }
  }

  &--close-button-visible {
    input[type="text"]#{$input},
    input[type="password"]#{$input},
    input[type="number"]#{$input}  {
      @apply pr-2xl+2;

      &:focus:enabled {
        @apply pr-2xl+2;
      }
    }
  }

  &--small-label {
    #{$base}__label-container {
      @apply pt-none+4;
    }
    #{$base}__label {
      @apply text-caption-lg;
    }
  }
}
